import React, {
  FunctionComponent,
  useCallback,
  useEffect,
  useState,
} from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { useFetch, useLocalStorage, useModalLocation } from "hooks";
import { ReactComponent as FaucetPayLogo } from "assets/logos/FaucetPay.svg";
import { CircularProgress, Transition, Typography } from "components";

import { modalsLoaderColor } from "sass/styleVariables";
import { CopyInput, Button } from "../../../componentsProgressive";
import { Template } from "../Components";
import { DepositTypes } from "./types";
import GemInfo from "../GemInfo";
import css from "./style.module.scss";
import { getChainOnlyValue } from "../getChainOnlyValue";

interface Props {
  currency?: string;
}

const titleStyle = { marginBottom: "1rem" };

const Deposit: FunctionComponent<Props> = ({ currency }) => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");
  const { pushHistoryTo } = useModalLocation("fp-deposit");

  const [depositAddress, setDepositAddress] = useState<string | null>(null);
  const [qr, setQR] = useState("");
  const [showImg, setShowImg] = useState(false);
  const [isFPSupported, setIsFPSupported] = useState(false);
  const [amount, setAmount] = useState((0).toFixed(8));
  const [fpAmount, setFPAmount] = useState((0).toFixed(8));
  const [confirmations, setConfirmations] = useState("0");

  const [depositFetch, { data: depositData }] = useFetch<DepositTypes>(
    "/deposit/get-deposit-information",
  );

  const goFetch = () =>
    depositFetch({
      variables: {
        token,
        language: "en",
        coin: currency,
      },
    });

  useEffect(() => {
    if (currency) {
      goFetch();
    }
  }, [currency]);

  useEffect(() => {
    if (depositData) {
      const {
        address,
        qr,
        fp_supported,
        minimum_deposit_direct,
        minimum_deposit_fp,
        confirmations_required,
      } = depositData;

      setDepositAddress(address);
      setQR(qr);
      setAmount(minimum_deposit_direct);
      setConfirmations(confirmations_required);

      if (fp_supported === "1") {
        setIsFPSupported(true);
        setFPAmount(minimum_deposit_fp);
      } else {
        setIsFPSupported(false);
      }

      let timeout: NodeJS.Timeout;

      if (!address) {
        timeout = setTimeout(() => {
          goFetch();
        }, 2000);
      }

      return () => {
        clearTimeout(timeout);
      };
    }
  }, [depositData]);

  const handleFPDepositOpen = () => {
    pushHistoryTo({});
    setTimeout(() => {
      if (!currency) return;
      pushHistoryTo({ searchParams: { coin: currency } });
    }, 0);
  };

  const qrOnload = useCallback(() => {
    setShowImg(true);
  }, []);

  if (currency === "GEM") {
    return <GemInfo />;
  }

  return (
    <Template>
      <div className={css.content}>
        <img
          src={qr}
          alt="QR"
          className={clsx(css.qrCode, !showImg && css.qrDisable)}
          onLoad={qrOnload}
        />

        <div className={css.inputWrapper}>
          <Transition
            animation="fade"
            in={!depositAddress}
            className={css.loader}>
            <CircularProgress color={modalsLoaderColor} size={60} />
          </Transition>

          <Transition
            animation="fade"
            in={!!depositAddress}
            className={css.inputInnerWrapper}>
            <CopyInput
              id="deposit-input"
              className={css.input}
              readOnly
              label={`${intl.formatMessage({
                id: "wallet.deposit.addressLabel",
              })} (${currency || "Null"})`}
              value={depositAddress}
            />
          </Transition>
        </div>

        <div className={css.reminderBlock}>
          <Typography className={css.reminderTitle} style={titleStyle}>
            {intl.formatMessage({ id: "wallet.deposit.reminder" })}
          </Typography>

          <div className={css.reminderBlockContainer}>
            <div className={css.leftBlock}>
              {getChainOnlyValue(currency)}
              <Typography className={css.reminderText}></Typography>
              <Typography className={css.reminderText}>
                {`${intl.formatMessage({
                  id: "wallet.deposit.firstReminder",
                })} ${amount} ${currency || "Null"}`}
              </Typography>

              {isFPSupported && (
                <Typography className={css.reminderText}>
                  {`${intl.formatMessage({
                    id: "wallet.deposit.fpReminder",
                  })} ${fpAmount} ${currency || "Null"}`}
                </Typography>
              )}

              <Typography className={css.reminderText}>
                {`${intl.formatMessage({
                  id: "wallet.deposit.confReminder",
                })} ${confirmations}`}
              </Typography>

              {currency === "BTT" && (
                <Typography className={css.reminderText} style={titleStyle}>
                  {intl.formatMessage({ id: "wallet.deposit.BTTWarning" })}
                </Typography>
              )}

              <Typography className={css.reminderText}>
                {intl.formatMessage({ id: "wallet.deposit.secondReminder" })}
              </Typography>
              <Typography className={css.reminderText}>
                {intl.formatMessage({ id: "wallet.deposit.thirdReminder" })}
              </Typography>
            </div>
            {isFPSupported && (
              <div className={css.rightBlock}>
                <div className={css.rightBlockWrapper}>
                  <Typography className={css.facePayDepositTitle}>
                    OR DEPOSIT WITH
                  </Typography>
                  <FaucetPayLogo className={css.logo} />
                  <Button className={css.button} onClick={handleFPDepositOpen}>
                    {intl.formatMessage({ id: "wallet.deposit.button" })}
                  </Button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </Template>
  );
};

export default Deposit;
