import React from "react";
import clsx from "clsx";

import { Typography } from "components";

import css from "./style.module.scss";

const chainConfig: {
  [key: string]: {
    [key: string]: string[];
  };
} = {
  Chain: {
    BSC: ["BNB", "FLOKI", "CAKE", "DPET", "DXCT", "BUSD"],
    ETH: ["ETH", "FEY"],
    TRX: ["TRX", "BTT", "USDT"],
    "Avalanche C": ["AVAX"],
  },
  Mainnet: {
    Polygon: ["MATIC"],
  },
};

export const getChainOnlyValue = (currency?: string, className?: string) => {
  if (currency) {
    const returnValue = (value: string, type: string) => (
      <Typography className={clsx(css.reminderText, css.boldText, className)}>
        {value} {type} Only
      </Typography>
    );

    const checkCurrency = currency.toUpperCase();

    for (const type in chainConfig) {
      const folder = chainConfig[type];

      for (const coinType in folder) {
        const coinsList = folder[coinType];
        if (coinsList) {
          const isCurrentCoinPresentInArray = coinsList.findIndex(
            (value) => value === checkCurrency,
          );

          if (isCurrentCoinPresentInArray !== -1) {
            return returnValue(coinType, type);
          }
        }
      }
    }
  }

  return null;
};
